 @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');
*{
  padding: 0px;
  margin: 0px;
}
.wrapper{
  /* background-image:  */
  /* url('https://images.pexels.com/photos/998641/pexels-photo-998641.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1'); */
}

.logo{
  text-transform: uppercase;
  font-weight: "800";
  font-size: 40px;
  font-family: 'Poppins',sans-serif;
  letter-spacing: 3px;
  color: #3d535f;
  cursor: pointer;

}
.dot{
  font-size: 70px;
  color: #7f00ff;
}
.navItems{
  display: flex;
}
.navItems li{
  list-style: none;
  margin: 0 15px;
}
.navItems li a{
  font-size: 25px;
  color: #3d535f;
  letter-spacing: 3px;
  text-decoration: none;
  text-transform: capitalize;
}
.navItems li a:hover{
  color: #7f00ff;
}
.wrapper{
  display: flex;
  width: 100%;
  height:100vh;
  justify-content: center;
  align-items: center;
  padding: 0 10%;
  overflow: hidden; 
}
.wrapper::before{
  position: absolute;
  content: "";
  height: 600px;
  width: 600px;
  border-radius: 50%;
  left: -12%;
  top: 40%;
  background: linear-gradient(45deg,#7f00ff,pink);
}
.cols{
  width: 50%;
}
.cols0{
  z-index: 5;
}
.topline{
  display: flex;
  position: relative;
  font-size: 35px;
  letter-spacing: 5px;
  color: #3d535f;
}
.topline::after{
  position: absolute;
  left: 16%;
  content: "";
  height: 4px;
  width: 45px;
  bottom: 10px;
  background-color: #7f00ff;
}
.top div{
  border: 2px solid red;
}
.iam{
  display: block;
  font-size: 3.5em;
  font-weight: 700;
  color: #3d535f;
}
.multitext{
  color: #7f00ff;
  text-transform: capitalize;
  width:"200px";
  border:2px solid red;
}
.btns{
  width: 100%;
  position: relative;
  left: 150px;
 
}
.words{
  border: 2px solid blue;
  width: 700px;
}
.btns button{
  border: 1px solid rgb(0, 255, 136);
  background-color: #3d535f;
  color: white;
  cursor: pointer;
  padding: 8px 14px;
  margin: 20px 5px;
  letter-spacing: 2px;
  text-transform: capitalize;
  box-shadow: 0 15px;


}
.btns button:hover{
background-color: #7f00ff;
}
.imagebox img{
  position: relative;
  height: 100%;
  width: calc(130%-80px);
  top: -100px;
  right: 30px;
  animation: animateUser 4s linear infinite;
  /* border: 2px solid red; */
}
@keyframes animateUser {
  50%{
    right: 30px;
    top: -90px;
  }

}
.imagebox #splash{
  position: absolute;
  top: 50%;
  left: 80%;
  transform: translate(-70%,-50%) rotate(-35deg);
  width: 400px;
  filter: saturate(200%);
  height: 400px;
  animation: animate 4s linear infinite;
}
@keyframes animate {
  50%{
    top: 49%;
  left: 79%;
  /* width: 155%; */
  }

}